<template>
  <div class="chat">
    <div class="chat__sidebar">
      <h3></h3>
      <div id="users">People</div>
    </div>
  
    <div class="chat__main">
  
      <div class="chat__messages">
        <div class="message-item" v-for="message in messages" :key="message.createdAt">
          <div v-if="message && message.url">
            {{message.from}} <a target="_blank" v-bind:href="message.url">My current location</a>
          </div>
          <div v-else>
            {{message.from}}:{{message.text}}
          </div>
        </div>
      </div>
  
      <div class="chat__footer">
        <form id='message-form'>
          <input name='message' type='text' placeholder="Put message here" ref="newMessage" autofocus autocomplete="off" />
          <button v-on:click="send" > 
            Send
        </button>
        </form>
        <button ref="sendLocButton" id="send-location" v-on:click="sendLocation">{{sendLocationButtonText}}</button>
  
      </div>
    </div>
  </div>
</template>

<script>
import * as moment from 'moment';

  export default {
    name: 'Chat',
    props: {},
  
    data: function() {
      return {
        messages: [],
        sendLocationButtonText:"Send Location"
      }
    },
    sockets: {
      connect(e) {
        console.log('connected', e)
        this.$options.sockets.newMessage = (message) => {
          message.createdAt=moment( message.createdAt).format()
          this.addNewMessage(message)
        }
        this.$options.sockets.newLocationMessage = (message) => {
          console.log('location', message)
          message.createdAt=moment( message.createdAt).format()
          this.addNewMessage(message)
        }
  
      },
      customEmit: function(val) {
        console.log('this method was fired by the socket server. ', val)
      },
      disconnect() {}
    },
    methods: {
      addNewMessage: function(message) {
        this.messages.push(message)
      },
      send: function(e) {
        e.preventDefault();
        const text = this.$refs.newMessage.value;
        this.$socket.emit('createMessage', {
          from: 'User1',
          text
        }, () => {
          console.log('suuukaaa')
          this.$refs.newMessage.value = ""
  
        })
      },
      sendLocation: async function() {
        this.$refs.sendLocButton.disabled = "disabled"
        this.sendLocationButtonText = "Sending..."
  
        if (!navigator.geolocation) {
          return alert('Geolocation not supported by your browser')
        }
        await navigator.geolocation.getCurrentPosition(
          (position) => {
            const {
              coords: {
                latitude,
                longitude
              }
            } = position
            this.$socket.emit('createLocationMessage', {
                latitude,
                longitude
              },
              () => {
                this.$refs.sendLocButton.disabled = ""
        this.sendLocationButtonText = "Send Location"

              }
            )
          },
          (e) => {
            alert('Unable to fetch location')
            this.$refs.sendLocButton.disabled = "";
            this.$refs.sendLocButton.text = "Send location";

          })
      }
    }
  }
</script>

